<template>
  <div id="container">
    <div id="settings">
      <div class="user-pic">
        <img v-if="$store.state.photo !== 'https://server.gold-ticket.uz/storage/app/'" :src="$store.state.photo" :alt="$store.state.name">
        <img v-else src="https://gold-ticket.uz/tgs/avatar.png" :alt="$store.state.name">
      </div>
<!--      <div class="user-name">{{ this.first_name }} {{ this.last_name }}</div>-->
      <form class="settings-form" action="">
        <div class="form-control">
          <label class="form-label" for="name" v-if="$store.state.lang === 'ru'">Имя</label>
          <label class="form-label" for="name" v-else>Isim</label>
          <input class="form-input" id="name" name="name" v-model="name" type="text">
        </div>
        <div class="form-control">
          <label class="form-label" for="phone" v-if="$store.state.lang === 'ru'">Телефон</label>
          <label class="form-label" for="phone" v-else>Telefon</label>
          <input class="form-input" id="phone" name="phone" v-model="phone" :disabled="!!phone" type="text">
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "Settings",
  data: () => ({
    name: '',
    phone: '',
  }),
  async mounted() {
    // await this.$store.dispatch("getUser", Telegram.WebApp.initDataUnsafe.user.id)
    this.name = this.$store.state.name
    this.phone = this.$store.state.phone

    Telegram.WebApp.BackButton.show()
    Telegram.WebApp.BackButton.onClick(() => {
      Telegram.WebApp.MainButton.hide()
      Telegram.WebApp.showPopup(false)
      Telegra
      this.$router.push('/')
    });

    Telegram.WebApp.MainButton.show()
    if (this.$store.state.lang === 'ru') {
      Telegram.WebApp.MainButton.setParams({
        'text': "Сохранить",
        'color': "#f2c058",
        'text_color': "#FFF"
      })
    } else {
      Telegram.WebApp.MainButton.setParams({
        'text': "Saqlash",
        'color': "#f2c058",
        'text_color': "#FFF"
      })
    }
    Telegram.WebApp.MainButton.onClick(async () => {
      Telegram.WebApp.MainButton.showProgress()
      await this.submit()
    })
  },
  methods: {
    async submit() {
      await this.$store.dispatch('updateUser', {
        'user_id': Telegram.WebApp.initDataUnsafe.user.id,
        'data': {
          'name': this.name,
          'phone': this.phone
        }
      })
    }
  }
}
</script>

<style scoped>
#container {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (min-height: 734px) {
  #container {
    width: 100%;
    height: auto;
    padding: 0 35px;
    margin-bottom: 0;
    position: relative;
    top: 90px;
  }
}

@media screen and (min-height: 700px) {
  #container {
    width: 100%;
    height: auto;
    padding: 0 35px;
    margin-bottom: 0;
    position: relative;
    top: 90px;
  }
}

@media screen and (min-height: 498px) {
  #container {
    width: 100%;
    height: auto;
    padding: 0 35px;
    margin-bottom: 200px;
    position: relative;
    top: 80px;
  }
}
</style>
